import styled from "styled-components";
import { Helmet } from "react-helmet";
import { movieList } from "./api";
import { useState } from 'react';
import { Link, Outlet, useLocation, useMatch, useNavigate, useOutletContext, useParams } from "react-router-dom";
import { useQuery } from "react-query";
import { AnimatePresence, motion } from "framer-motion";
import LatestMovies from "./MovieF/LatestMovies";
import TopRatedMovies from "./MovieF/TopRatedMovies";
import Upcoming from "./MovieF/Upcoming";

export const Main = styled.div`
    width: 100%;
    //    height: 100vh;
`

export const ToggleThemeBtn = styled.button`
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
    border-radius: 0.3rem;
    font-weight: 900;
    text-align: center;
    cursor: pointer;
    transition: ease background-color 250ms;
    border: 1px solid black;
    &:hover{
        background-color: white;
        color: black;
    }
    position: fixed;
    bottom: 1rem;
    right: 1rem;
`
export interface movieData {
    adult: boolean;
    backdrop_path: string;
    id: number;
    original_language: string;
    original_title: string;
    overview: string;
    popularity: number;
    poster_path: string;
    release_date: string;
    title: string;
    video: boolean;
    vote_average: number;
    vote_count: number;
};


export const Wrapper = styled.div`
    color: ${(props) => props.theme.bodyFtColor};
    overflow-x: hidden;
`;

export const Loader = styled.div`
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const Banner = styled.div<{ bgPhoto: string | undefined }>`
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10%;
  background-image: linear-gradient(rgba(0,0,0,0) 20%, ${(props) => props.theme.bodyBgColor}), url(${(props) => props.bgPhoto});
  background-size: cover;
  height: 100vh;
`;



export const Title = styled.p`
  margin-bottom: 5%;
  font-size: 80%;
`;

export const Overview = styled.p`
  font-size: 10%;
  width: 100%;
`;

export const Slider = styled.div`
  top: -100px;
  position: relative;
  height: 40vh;
  @media screen and (max-width: 550px){
        height: 125vh;
    }
`;

export const Row = styled(motion.div)`
  display: grid;
  gap: 10px;
  @media screen and (max-width: 550px){
        grid-template-columns: repeat(2,1fr);
    }
    grid-template-columns: repeat(6, 1fr);
    width: 100%;
    margin-top: 10px;
    position: absolute;

`;

export const Box = styled(motion.article) <{ posterbg: string | undefined }>`
  height: 40vh;
  font-size: 100%;
  background-image: url(${(props) => props.posterbg});
  &:first-child {
    transform-origin: center left;
  }
  &:last-child {
    transform-origin: center right;
  }
`;

export const boxVariants = {
    normal: {
        scale: 1,
    },
    hover: {
        scale: 1.2,
        y: -80,
        transition: {
            delay: 0.3,
            duaration: 0.3,
            type: "tween",
        },
    },
};

export const rowVariants = {
    hidden: {
        x: window.outerWidth + 10,
    },
    visible: {
        x: 0,
    },
    exit: {
        x: -window.outerWidth - 10,
    },
};

export const Info = styled(motion.div)`
  background-color: ${(props) => props.theme.hoverNavItem};
  opacity: 0;
  position: absolute;
  width: 100%;
  bottom: 0;
  p {
    text-align: center;
    font-size: 0.3rem;
  }
`;

export const infoVariants = {
    hover: {
        opacity: 1,
        transition: {
            delay: 0.3,
            duaration: 0.3,
            type: "tween",
        },
    },
};
/*  */
export const BoxModal = styled(motion.div)`
  width: 10rem;
  height:  8rem;
  border-radius: 10px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.06);
  background-color: ${(props) => props.theme.bodyBgColor};
`;


export const overlay = {
    hidden: { backgroundColor: "rgba(0, 0, 0, 0)" },
    visible: { backgroundColor: "#3b3636c5" },
    exit: { backgroundColor: "rgba(0, 0, 0, 0)" },
};

export const Overlay = styled(motion.div)`
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
`;


export const BigCover = styled.div<{ bgPhoto: string | undefined }>`
  width: 100%;
  background-size: cover;
  background-position: center center;
  background-image: url(${(props) => props.bgPhoto});
  height: 70%;
`;

export const BigTitle = styled.h3`
  color: ${(props) => props.theme.bodyFtColor};
  font-size: 0.4rem;
  position: relative;
  padding: 5px;
`;

export const BigOverview = styled.p`
  font-size: 0.2rem;
  position: relative;
  color: ${(props) => props.theme.bodyFtColor};
  padding: 5px;
`;

const Home = () => {
    /*   */
    const { isLoading, data } = useQuery<movieData[]>(["movies"], movieList);
    /*   */
    const [themeText, setThemeText] = useState("  ");
    const [setIsDark] = useOutletContext<React.Dispatch<React.SetStateAction<boolean>>[]>();
    const toggleTheme = () => {
        setIsDark((element) => (!element));
        themeText === "  " ? setThemeText("  ") : setThemeText("  ")
    };
    const [index, setIndex] = useState(0);

    const [leaving, setLeaving] = useState(false);
    const [content, setContent] = useState<movieData>();
    const incraseIndex = () => {
        if (data) {
            if (leaving) return;
            else {
                const dataLength = Math.floor(data?.length / 6); // 5
                setLeaving(true);
                setIndex((prev) => dataLength - 1 > index ? prev + 1 : 0);
            }
        }
    }

    /* onExitComplete :    
        boxs  
             
        
    onExitComplete        false 
    */
    /*  */
    //const pricematch = useMatch("movie/:movieId");
    const [id, setId] = useState<null | string>(null);
    const navigate = useNavigate();
    const bigMovieMatch = useMatch("/movies/:movieId");
    return (
        <>
            <Helmet>
                <title>B-Grade</title>
            </Helmet>
            <Main>
                <ToggleThemeBtn onClick={toggleTheme}>{themeText}</ToggleThemeBtn>
                <Wrapper>
                    {isLoading ? (
                        <Loader>Loading...</Loader>
                    ) : (
                        <>
                            <Banner
                                onClick={incraseIndex}
                                bgPhoto={`https://image.tmdb.org/t/p/original/${data?.[0].backdrop_path}`}>
                                <Title>{data?.[0].original_title}</Title>
                                <Overview>{data?.[0].overview}</Overview>
                            </Banner>
                            <Slider>
                                <AnimatePresence
                                    initial={false} onExitComplete={() => {
                                        setLeaving((prev) => !prev);
                                    }}>{/* AnimatePresence Slider key  */}
                                    <Row
                                        variants={rowVariants}
                                        initial="hidden"
                                        animate="visible"
                                        exit="exit"
                                        transition={{ type: "tween", duration: 1 }}
                                        key={index}
                                    >
                                        {/* Row index 0   , random   */}
                                        {data?.slice(1).slice(6 * index, (6 * (index + 1))).map((i) => (
                                            /*  Box  unique key   */
                                            <Box key={i.id}
                                                posterbg={`https://image.tmdb.org/t/p/w200/${i.poster_path}`}
                                                whileHover="hover"
                                                initial="normal"
                                                variants={boxVariants}
                                                transition={{ type: "tween" }}
                                                onClick={() => {
                                                    setId(`${i.id}`);
                                                    setContent(i);
                                                    navigate(`movie/${i.id}`);
                                                }} layoutId={`${i.id}`}
                                            >
                                                <Info key={i.id} variants={infoVariants}>
                                                    <p>{i.title}</p>
                                                </Info>
                                            </Box>
                                        ))}
                                    </Row>
                                </AnimatePresence>
                            </Slider>
                            <AnimatePresence>
                                {id ? (
                                    <Overlay
                                        variants={overlay}
                                        onClick={() => {
                                            setId(null)
                                            navigate("");
                                        }}
                                        initial="hidden"
                                        animate="visible"
                                        exit="exit"
                                    >
                                        <BoxModal layoutId={id}>
                                            <BigCover bgPhoto={`https://image.tmdb.org/t/p/original/${content?.backdrop_path}`} />
                                            <BigTitle>{content?.title}</BigTitle>
                                            <BigOverview>{content?.overview}</BigOverview>
                                        </BoxModal>
                                    </Overlay>
                                ) : null}
                            </AnimatePresence>
                        </>
                    )}
                </Wrapper>
                <LatestMovies></LatestMovies>
                <TopRatedMovies></TopRatedMovies>
                <Upcoming></Upcoming>
            </Main>
        </>
    );
};
export default Home;
